import React, { Component } from 'react';
import axios from 'axios';
import Card from '../../../components/Card/Card';
import { Link } from 'react-router-dom';
import './Cards.css';


class Cards extends Component {
    state={
        components: []
    }

    componentDidMount() {
        axios.get('http://demo8401127.mockable.io/components').then(response => {
            const trimmedComponents = response.data.components.slice(0, 10);
            const displayComponents = trimmedComponents.map(component=> {
                return {
                    ...component,
                    author: 'Sujay'
                };
            });
            console.log(displayComponents);
            this.setState({
                components: displayComponents
        })});
    }
    
    render() {
        let components = this.state.components.map(component=> {
                return (
                    <Link to={'/' + component.id} key={component.id} style={{textDecoration: 'none'}}>
                        <Card name={component.name} pass={component.pass} prev_pass={component.previous_pass}/>
                    </Link>
                );
            });

        return (
            <div>
                <section className="Cards">
                    {components}
                </section>
            </div>
        );
    }
}

export default Cards;
